<template>
    <div class="wrap">
        <div class="top">
            <div class="title"><h3></h3></div>
            <div class="open-modal">
                <button class="btn btn-primary" @click="openModal(true)">
                    
                </button>
            </div>
        </div>
        <div class="table-wrap">
            <table class="table table-borderless">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">(%)</th>
                        <th scope="col"></th>
                        <th scope="col"></th>
                        <th scope="col"></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in products" :key="item.id">
                        <th>{{ item.name }}</th>
                        <td>{{ item.discount }}</td>
                        <td>{{ moment(item.expiry_date).calendar() }}</td>
                        <td v-if="item.enable === 1"></td>
                        <td v-if="item.enable === 0"></td>
                        <td class="operate-btn">
                            <button @click="openModal(false, item)">
                                <i class="fas fa-edit"></i>
                            </button>
                            <button @click="delCoupon(item.id)">
                                <i class="fas fa-trash"></i>
                            </button>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
        <div class="page-arrow">
            <i class="fas fa-arrow-left" @click="previousPage"></i>
            <i class="fas fa-arrow-right" @click="nextPage"></i>
        </div>
        <div
            class="modal fade"
            id="productModal"
            tabindex="-1"
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
        >
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content border-0">
                    <div class="modal-header bg-dark text-white">
                        <h5 class="modal-title" id="exampleModalLabel">
                            <span></span>
                        </h5>
                        <button
                            type="button"
                            class="close"
                            data-dismiss="modal"
                            aria-label="Close"
                        >
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <div class="row">
                            <div class="col-sm-8">
                                <div class="form-group">
                                    <label for="title"></label>
                                    <input
                                        type="text"
                                        class="form-control"
                                        id="title"
                                        v-model="tempProduct.name"
                                        placeholder=""
                                    />
                                </div>
                                <div class="form-row">
                                    <div class="form-group col-md-6">
                                        <label for="category"></label>
                                        <input
                                            type="text"
                                            class="form-control"
                                            id="category"
                                            v-model="tempProduct.code"
                                            placeholder=""
                                        />
                                    </div>
                                </div>

                                <div class="form-row">
                                    <div class="form-group col-md-8">
                                        <label for="origin_price"></label>
                                        <input
                                            type="number"
                                            class="form-control"
                                            id="origin_price"
                                            v-model="tempProduct.discount"
                                            placeholder=""
                                        />
                                    </div>
                                </div>
                                <hr />
                                <div class="form-group">
                                    <label for="due_date"></label>
                                    <el-date-picker
                                        v-model="tempProduct.expiry_date"
                                        type="date"
                                        placeholder=""
                                    >
                                    </el-date-picker>
                                </div>
                                <div class="form-group">
                                    <div class="form-check">
                                        <input
                                            class="form-check-input"
                                            type="checkbox"
                                            v-model="tempProduct.enable"
                                            :true-value="1"
                                            :false-value="0"
                                            id="is_enabled"
                                        />
                                        <label
                                            class="form-check-label"
                                            for="is_enabled"
                                        >
                                            
                                        </label>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button
                            type="button"
                            class="btn btn-outline-secondary"
                            data-dismiss="modal"
                        >
                            
                        </button>
                        <button
                            type="button"
                            class="btn btn-primary"
                            @click="updateProduct"
                        >
                            
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
var moment = require("moment");
import axios from "axios";
import $ from "jquery";

export default {
    name: "Coupon",
    data() {
        return {
            moment: moment,
            products: [],
            page: 1,
            total: "",
            tempProduct: {},
            isNew: false,
            isLoading: false,
            imageUrl: null,
            status: {
                fileUploading: false
            }
        };
    },
    created() {
        this.getCoupons();
    },
    methods: {
        getCoupons() {
            axios.get(`/api/admin/coupons/page=${this.page}`).then(res => {
                this.products = res.data.data;
                this.total = res.data.total;
            });
        },
        previousPage() {
            let page = this.page - 1;
            if (page > 0) {
                axios.get(`/api/admin/coupons/page=${page}`).then(res => {
                    this.products = res.data.data;
                    this.page = page;
                });
            } else {
                this.$notify.info({
                    title: "",
                    message: "(`)",
                    duration: 1500
                });
            }
        },
        nextPage() {
            let page = this.page + 1;
            if (page <= this.total) {
                axios
                    .get(`/api/admin/coupons/page=${page}`)
                    .then(res => {
                        this.products = res.data.data;
                        this.page = page;
                    })
                    .catch(err => {
                        console.log(err);
                    });
            } else {
                this.$notify.info({
                    title: "",
                    message: "(`)",
                    duration: 1500
                });
            }
        },
        openModal(isNew, item) {
            if (isNew) {
                this.tempProduct = {
                    enable: 0
                };
                this.isNew = true;
            } else {
                this.tempProduct = Object.assign({}, item);
                this.isNew = false;
            }
            $("#productModal").modal("show");
        },
        updateProduct() {
            const vm = this;
            if (!vm.isNew) {
                axios
                    .put("/api/coupons/update", vm.tempProduct)
                    .then(res => {
                        if (res.data.result === true) {
                            this.$notify({
                                title: "",
                                message: "",
                                type: "success",
                                duration: 1500
                            });
                            $("#productModal").modal("hide");
                            this.getCoupons();
                        }
                    })
                    .catch(err => {
                        console.log(err);
                    });
            } else {
                axios
                    .post("/api/coupons/upload", vm.tempProduct)
                    .then(res => {
                        if (res.data.result === true) {
                            this.$notify({
                                title: "",
                                message: "",
                                type: "success",
                                duration: 1500
                            });
                            $("#productModal").modal("hide");
                            this.getCoupons();
                        } else {
                            this.$notify.error({
                                title: "",
                                message: "",
                                duration: 1500
                            });
                        }
                    })
                    .catch(err => {
                        console.log(err);
                    });
            }
        },
        openDelProductModal(item) {
            const vm = this;
            $("#delProductModal").modal("show");
            vm.tempProduct = Object.assign({}, item);
        },
        delCoupon(id) {
            this.$confirm(", ?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning"
            })
                .then(() => {
                    axios
                        .delete(`/api/coupons/delete/${id}`)
                        .then(res => {
                            if (res.data.result === true) {
                                this.$notify({
                                    title: "",
                                    message: "",
                                    type: "success",
                                    duration: 1500
                                });
                                this.getCoupons();
                            } else {
                                this.$notify.error({
                                    title: "",
                                    message: "",
                                    duration: 1500
                                });
                            }
                        })
                        .catch(err => {
                            console.log(err);
                        });
                })
                .catch(() => {
                    this.$notify.info({
                        title: "",
                        message: "",
                        duration: 1500
                    });
                });
        }
    }
};
</script>
